.wrapper--tooltip__label {
    @include smalltype();

    background-color: $color-gray10;
    display: flex;
    position: absolute;
    right: 0;
    top: 0;
    z-index: z-index('above');

    &[data-open='false'] {
        .tooltip__label {
            display: none;
        }
    }

    .tooltip__label {
        color: $color-purewhite;
        padding: $measure--half;

        &:focus {
            @include outline-properties($color-purewhite);
            outline-offset: -#{$border-width-default};
        }

        a {
            @include anchor-discreet--notransition(
                $color-purewhite,
                $color-gray3,
                $color-purewhite,
                $color-purewhite,
                $color-purewhite
            );
        }

        .tooltip__label__licence {
            display: table;
        }
    }

    .tooltip--image-credit {
        @include smalltype();
        background-color: $color-gray10;
        border: 0;
        color: $color-purewhite;
        font-weight: $font-weight-normal;
        margin: 0;
        padding: $measure--half;
        width: auto;
        pointer-events: auto;
        &:hover {
            @include media('hover') {
                background-color: $color-gray8;
            }
        }

        &:active,
        &:focus {
            outline: 0;

            .icon {
                outline: $border-width-default $border-style-dotted
                    $color-purewhite;
            }
        }

        .icon {
            padding: 0.25rem;
            font-size: $font-size--alpha;
        }
    }
}

.card--news {
    .wrapper--tooltip__label {
        right: $border-width-thick;
        top: $border-width-thick;
    }

    .wrapper--tooltip__label ~ .card__anchor {
        -moz-transition: filter 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
        -webkit-transition: filter 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
        transition: filter 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
    }

    .wrapper--tooltip__label[data-open='true'] ~ .card__anchor {
        -webkit-filter: brightness(0.3);
        filter: brightness(0.3);
    }
}
